.bg-balls {
  position: fixed;
  width: 100vw;
  max-width: 1920px;
  max-height: 100vh;
  justify-content: space-between;
  margin: 0 auto;
  overflow: visible;
  opacity: 0;
  -webkit-transition: opacity 3s ease-in-out;
  -moz-transition: opacity 3s ease-in-out;
  transition: opacity 3s ease-in-out;
}
.bg-balls.showing {
  opacity: 1;
}

.ball {
  position: absolute;
  border-radius: 50%;
}

.s3, .s4 {
  right: -5vw;
  bottom: -10vw;
  width: 68vw;
  height: 68vw;
  min-width: 500px;
  min-height: 500px;
  background-color: black;
  border-radius: 50%;
  box-shadow: inset 50px 50px 10vh #d4e4f74f, 20px 20px 10vh #dbdef045;
  -webkit-transition: transform 1.5s ease-out;
  -moz-transition: transform 1.5s ease-out;
  transition: transform 1.5s ease-out;
  transform: translateZ(3px);
}

.s4 {
  left: 30vw;
  bottom: -50vw;
  width: 70vw;
  height: 70vw;
  transform: translateZ(4px);
}

.chain-grid {
  position: absolute;
  top: 1000px;
  left: 50%;
  width: 50vw;
  height: auto;
  max-width: 700px;
  max-height: 591px;
  -webkit-transition: transform 1.5s ease-out;
  -moz-transition: transform 1.5s ease-out;
  transition: transform 1.5s ease-out;
  transform: translateZ(5px);
}
@media (min-width: 46em) {
  .chain-grid {
    width: 30vw;
  }
}

@keyframes moveChainGrid {
  0% {
    transform: translateY(30px);
  }
  50% {
    transform: translateY(0px);
  }
  100% {
    transform: translateY(30px);
  }
}